.header{background-color:white;max-width:100%;padding:15px 20px;margin:0px auto;font-family:"KoHo"}.headerDark{background-color:#171c28}.headerDark li a{display:block;padding:15px 20px;color:#fdfcfa !important;text-decoration:none}.header ul{margin:0;padding:0;list-style:none;overflow:hidden}.greyColor{color:gray}.header li a{display:block;padding:15px 20px;color:black;text-decoration:none}.header li a:hover,.header .menuBtn:hover{color:#191970;font-weight:bolder}.headerDark li a:hover,.headerDark .menuBtnDark:hover{color:#0FFFFF !important}.logoName{color:#191970;font-family:"Tangerine";font-size:larger}.logoNameDark{color:#0FFFFF !important}.header .logo{display:block;flex:none;float:left;font-size:1.5em;text-decoration:none;margin-top:10px;line-height:normal}.header .menu{clear:both;max-height:0;transition:max-height 0.2s ease-out}.header .menuIcon{cursor:pointer;display:inline-block;float:right;padding:28px 20px;position:relative;-webkit-user-select:none;-ms-user-select:none;user-select:none}.header .menuIcon .navicon{background-color:#191970;display:block;height:2px;position:relative;transition:background 0.2s ease-out;width:18px}.headerDark .menuIconDark .naviconDark{background-color:#0FFFFF;display:block;height:2px;position:relative;transition:background 0.2s ease-out;width:18px}.header .menuIcon .navicon:before,.header .menuIcon .navicon:after{background:#191970;content:"";display:block;height:100%;position:absolute;transition:all 0.2s ease-out;width:100%}.header .menuIcon .navicon:before{top:5px}.header .menuIcon .navicon:after{top:-5px}.headerDark .menuIconDark .navicon::before,.headerDark .menuIconDark .navicon::after{background:#0FFFFF;content:"";display:block;height:100%;position:absolute;transition:all 0.2s ease-out;width:100%}.header .menuBtn{display:none}.header .menuBtn:checked ~ .menu{max-height:512px}.header .menuBtn:checked ~ .menuIcon .navicon{background:transparent}.header .menuBtn:checked ~ .menuIcon .navicon:before{-webkit-transform:rotate(-4